@import url('https://fonts.googleapis.com/css2?family=Noto+Sans+SC:wght@100..900&family=Red+Hat+Display:ital,wght@0,300..900;1,300..900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Red+Hat+Display:ital,wght@0,300..900;1,300..900&display=swap');

$ch-family:"Noto Sans SC", sans-serif;
$en-family:"Red Hat Display",
sans-serif;
$normal-family:'Montserrat',
sans-serif;

$black:#0c131b;

html,
body {
    -webkit-text-size-adjust: 100%; // for mobile fontsize
}

a {
    text-decoration: none;
    color: inherit;

    img {
        border: 0;
    }
}

:focus {
    outline: 0;
}

strong,
b {
    font-weight: bold;
}

button {
    background-color: transparent;
    border: 0;
}

* {
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
}

img {
    vertical-align: middle; //remove bottom space
    max-width: 100%;
    // max-height: 100%;
}

/* override the default IOS styles */
input,
textarea {
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
}


